}

.slider {
  transform: translateX(-100%);
  transition: transform 0.6s ease-out;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 30vw;
  margin-top: 180px;
  position: relative;
  perspective: 1000px;
  transform-style: preserve-3d;
  z-index: 1;
}
label img {
  border-radius: 4px;
}
.slider label {
  margin: auto;
  width: 50%;
  height: 100%;
  border-radius: 4px;
  position: absolute;
  left: 0;
  right: 0;
  cursor: pointer;
  transition: transform 0.4s ease;
}

